/**
 * Custom Vanilla Styling
 */
html, body {
  background-color: #EAEAEA; }

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 1.25em; }

#flashContainer {
  padding-top: 61px; }

.no-padding {
  padding: 0; }

.dropFile {
  padding: 15px;
  border: 2px #f1f1f1 dashed;
  border-radius: 5px;
  line-height: 34px; }

/**
 * Fields
 */
.btn {
  margin: .5em; }

