/**
 * Lists
 */
.md-list-item {
  padding: 10px 0; }

.md-list-item span {
  color: rgba(0, 0, 0, 0.54); }

/**
 * Nav
 */
.navbar-toggle {
  background-color: #000;
  float: left;
  margin-left: .5em; }
  @media (min-width: 0) {
    .navbar-toggle {
      display: block;
      /* force showing the toggle */ } }

@media (min-width: 768px) {
  .navbar-right {
